/* Global */
/*================================================*/
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #1e1e1e;
	color: #ededed;
	font-family: Arial, Helvetica, sans-serif;
}

li {
	list-style: none;
}

button {
	background-color: #ff5722;
	color: #ededed;
	border: none;
	padding: 10px 20px;
	border-radius: 8px;
	cursor: pointer;
	transition: background-color 0.3s ease;
}

button:hover {
	background-color: #e64a19;
}

input,
label {
	color: #ededed;
	background-color: #1e1e1e;
	border: 1px solid #272727;
	border-radius: 5px;
	padding: 5px;
}

input:focus {
	outline: none;
	border-color: #ff5722;
}

label {
	margin: 5px 0;
}

.prevent-select {
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

/*================================================*/
/* Nav Section */
/*================================================*/
nav {
	display: flex;
	justify-content: space-between;
	padding: 20px;
	background-color: #1e1e1e;
	border-bottom: 1px solid #272727;
}

h1 a {
	color: #ff5722;
	text-decoration: none;
}

/*================================================*/
/* Dialog window */
/*================================================*/
::backdrop {
	background-color: rgba(0, 0, 0, 0.7);
}

#add-task {
	margin: 10% auto;
	border-radius: 20px;
	height: 450px;
	width: 300px;
	background-color: #1e1e1e;
	color: #ededed;
	border: 1px solid #272727;
}

.close,
.close-modal {
	color: #aaa;
	float: right;
	font-size: 28px;
	font-weight: bold;
	cursor: pointer;
}

.dialog-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.dialog-container div {
	display: flex;
	flex-direction: column;
	margin-bottom: 10px;
}

.dialog-container span {
	margin-right: 5px;
	align-self: flex-end;
}

input[type='radio'] {
	margin-top: 10px;
	background-color: #1e1e1e;
}

.dialog-container input[type='submit'] {
	background-color: #ff5722;
	color: #ededed;
	border: none;
	padding: 10px 20px;
	border-radius: 8px;
	cursor: pointer;
	margin-top: 30%;
}

.dialog-container input[type='submit']:hover {
	background-color: #e64a19;
}

/*================================================*/
/* Main Section */
/*================================================*/

main {
	display: grid;
	grid-template-columns: 200px 1fr;
}

/*================================================*/
/* Project Container */
/*================================================*/

.project-container,
.task-container {
	background-color: #1e1e1e;
	border: 1px solid #272727;
	padding: 20px;
	border-radius: 10px;
}

.project-item {
	color: #ededed;
	padding: 10px;
	border-radius: 5px;
	transition: background-color 0.3s ease;
}

.project-item:hover {
	background-color: #272727;
	cursor: pointer;
}

.project-item:hover {
	cursor: pointer;
}

/*================================================*/
/* Task Container */
/*================================================*/
.task-list {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 20px;
}

.task-item {
	background-color: #1e1e1e;
	padding: 15px;
	border-radius: 15px;
	width: 60%;
	border: 1px solid #272727;
	transition: transform 0.3s ease, background-color 0.3s ease;
}

.task-item:hover {
	transform: scale(1.03);
	background-color: #272727;
	cursor: pointer;
}

.task-item.low {
	border-left: 5px solid #4caf50;
}

.task-item.medium {
	border-left: 5px solid #ffc107;
}

.task-item.high {
	border-left: 5px solid #f44336;
}

/*================================================*/
/* Task Information Screen */
/*================================================*/
.task-information {
	display: none;
	position: fixed;
	z-index: 1;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.8);
	color: #ededed;
}

.information-container {
	background-color: #fefefe;
	margin: 10% auto;
	height: 300px;
	width: 500px;
	border: none;
	border-radius: 20px;
	-webkit-box-shadow: 5px 5px 7px 0px rgba(0, 0, 0, 0.44);
	box-shadow: 5px 5px 7px 0px rgba(0, 0, 0, 0.44);
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #1e1e1e;
	color: #ededed;
}

.information-container span {
	align-self: flex-end;
	margin-right: 5px;
}
